import React from 'react'
import { Field, IFieldApi } from 'react-form'

import { WithRoot } from '../../../atoms/WithRoot'
import { GMenuRecipe, WareCategoryTypeEnum } from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { RecipeListTable } from '../molecules/RecipeListTable'
import { RecipeSuggestionPanel } from './RecipeSuggestionPanel'

interface IProps {
  categoryType: WareCategoryTypeEnum
}

export const CategoryRecipesView: React.SFC<IProps> = ({ categoryType }) => (
  <WithRoot
    observe={false}
    render={({
      settings: {
        menu: { recipe },
      },
    }) => (
      <Field field="recipes">
        {(fieldApi: IFieldApi<GMenuRecipe.Fragment[]>) => (
          <>
            <RecipeListTable
              recipes={fieldApi.value}
              isSortable={true}
              showRemoveColumn={true}
              showEditColumn={false}
              showVisibilityColumn={false}
              onRemove={recipeId =>
                fieldApi.setValue(
                  RM.reject(RM.propEq('id', recipeId), fieldApi.value),
                )
              }
              onSortRecipe={({ items }) => {
                fieldApi.setValue(items)
              }}
            />
            <RecipeSuggestionPanel
              categoryType={categoryType}
              filterRecipes={RM.without(fieldApi.value)}
              onAdd={addedRecipe =>
                fieldApi.setValue(fieldApi.value.concat(addedRecipe))
              }
            />
          </>
        )}
      </Field>
    )}
  />
)
